body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: rgb(213, 51, 62);
  font-family: 'Coda Caption', sans-serif;
  cursor: url(jesslodge.com/static/media/mouth.414092d0.png), url(https://imgur.com/8G5Dhcp);
  color: white;
  height: 100vh;
}
#defaultCanvas0 {
  z-index: -1;
  position: absolute;
}

.App {
  margin: 5%;
}

section {
  font-size: 12px;
  color: white
}

.Nav-header {
  text-align: left;
  color: white;
  /* overflow: hidden; */
  position: fixed; /* Set the navbar to fixed position */
  top: 0; /* Position the navbar at the top of the page */
  width: 100%; /* Full width */
  padding: 14px 16px;
}
.App-about {
  text-align: left;
  cursor:url("https://imgur.com/8G5Dhcp"), auto;
}

.App-about h1 {
  font-size: 60px;
  margin-bottom: 0;
}
.App-about h2 {
  font-size: 30px;
  margin-bottom: -10px;
}
.App-about h3 {
  font-size: 25px;
  margin-bottom: -10px;
}
.social-links i {
  color: white;
  padding-right: 10px;
}

.crumbs-image {
  background-image: url(jesslodge.com/static/media/crumbs.c27b2e93.png);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: rgb(213, 51, 62);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

